import {customElement} from '@here/hds-base';
import '@here/hds-icon';
import {IconCategory} from '@here/hds-iconlibrary';
import {html, LitElement, nothing, PropertyValues} from 'lit';
import {property, query} from 'lit/decorators.js';
import {classMap} from 'lit/directives/class-map.js';
import {styles} from './hds-accordion-item.css.js';
import {AccordionArrowPosition, AccordionChangePayload, AccordionVariant} from './hds-accordion.js';

/**
 * @summary Item for Accordion component
 * @slot default Content of the Accordion item
 *
 * @event {CustomEvent<{index: number; id: string; expanded: boolean}>} change Fired when the item has expanded/collapsed `AccordionChangePayload`
 */
@customElement('hds-accordion-item')
export class AccordionItem extends LitElement {
  static styles = styles;
  static itemIdCounter = 0;
  static shadowRootOptions = {...LitElement.shadowRootOptions, delegatesFocus: true};
  @property({type: String, attribute: false}) variant: AccordionVariant = 'default';
  @property({type: String, attribute: false}) arrowPosition: AccordionArrowPosition = 'end';
  @property({type: Boolean, reflect: true}) disabled = false;
  @property({type: Boolean, reflect: true, attribute: false}) isAccordionDisabled = false;
  @property({type: String, reflect: true}) headline = '';
  @property({type: Boolean, reflect: true}) expanded = false;
  @property({type: String}) icon?: string;
  @property({type: String, attribute: 'icon-category'}) iconCategory: IconCategory = 'core-ui';
  @property({type: Boolean, attribute: false}) isLastChild = false;
  @property({type: Boolean, attribute: false}) isFirstChild = false;
  @property({type: Number, attribute: false}) index = -1;
  @query('.-hds-accordion-button') buttonElement!: HTMLDivElement;

  connectedCallback() {
    super.connectedCallback();
    this.id = this.id || `hds-accordion-item-${++AccordionItem.itemIdCounter}`;
  }

  focus() {
    return this.buttonElement && this.buttonElement.focus();
  }

  blur() {
    return this.buttonElement && this.buttonElement.blur();
  }

  protected updated(_changedProperties: PropertyValues) {
    if (_changedProperties.has('expanded') && _changedProperties.get('expanded') !== undefined) {
      this.dispatchEvent(
        new CustomEvent<AccordionChangePayload>('change', {
          detail: {
            index: this.index,
            id: this.id,
            expanded: this.expanded,
          },
          bubbles: true,
        })
      );
    }
  }

  protected renderIcon() {
    if (!this.icon) return nothing;
    return html`
      <hds-icon
        class="icon"
        name="${this.icon}"
        category="${this.iconCategory}"
        size="16px"
      ></hds-icon>
    `;
  }

  protected render() {
    const classes = {
      disabled: this.disabled || this.isAccordionDisabled,
      expanded: this.expanded,
      'last-child': this.isLastChild,
      'first-child': this.isFirstChild,
    };
    classes[`hds-accordion-variant-${this.variant}`] = true;
    classes[`arrow-pos-${this.arrowPosition}`] = true;

    const buttonClasses = {
      disabled: this.disabled || this.isAccordionDisabled,
    };

    const arrowIcon = html` <hds-icon name="chevron-down" size="16px"></hds-icon>`;
    return html`
      <div class="-hds-accordion-item-wrapper ${classMap(classes)}">
        <div
          tabindex="0"
          role="button"
          class="-hds-accordion-button ${classMap(buttonClasses)}"
          @click="${this._toggle}"
          id="${`accordion-control-${this.index}`}"
          aria-expanded="${this.expanded}"
          aria-controls="accordion-section-${this.index}"
          aria-disabled="${this.expanded && (this.disabled || this.isAccordionDisabled)}"
        >
          <div class="headline">
            ${this.renderIcon()}
            <span class="headline-text">${this.headline}</span>
          </div>
          <div class="arrow">${arrowIcon}</div>
        </div>
        <div
          class="content"
          role="region"
          id="${`accordion-section-${this.index}`}"
          aria-labelledby="${`accordion-control-${this.index}`}"
        >
          <slot></slot>
        </div>
      </div>
    `;
  }

  private _toggle() {
    if (this.disabled || this.isAccordionDisabled) return;
    this.expanded = !this.expanded;
  }
}

declare global {
  interface HTMLElementTagNameMap {
    'hds-accordion-item': AccordionItem;
  }
}
